import React from 'react';
import ReactDOM from 'react-dom';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import AppBar from 'material-ui/AppBar';
import IconButton from 'material-ui/IconButton';
import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import NavigationClose from 'material-ui/svg-icons/navigation/close';
import { List, ListItem } from 'material-ui/List';
import "./index.css";

const baseUrl = 'http://rohanpooniwala.pythonanywhere.com'

class ChatApp extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			username_text: "",
			message_text: "",
			username: "test",
			id: -1,
			prevId: -1,
			color: "#444444",
			messages: [],
			newMessage: false,
			usercount: 0
		};

		setInterval(() => { this.get_messages() }, 500);

		this.handleChange = this.handleChange.bind(this);
		this.messageHandleChange = this.messageHandleChange.bind(this);

		window.onbeforeunload = (e) => {
			console.log("onReload");
			if (this.state.username !== "")
				this.user_server("/logout");
		};

		window.onunload = (e) => {
			console.log("onClose");
			if (this.state.username !== "")
				this.user_server("/logout");
		};
	}


	handleChange(event) {
		this.setState({ username_text: event.target.value });
	}


	messageHandleChange(event) {
		this.setState({ message_text: event.target.value });
	}

	render() {
		if (this.state.username === "") {
			return (
				<MuiThemeProvider>
					<div>
						<AppBar
							title="Chat App"
							iconElementLeft={<IconButton></IconButton>}
						/>

						<h1>Hello,</h1>
						<TextField
							hintText="Username."
							errorText=""
							onChange={this.handleChange}
						/><br />
						<RaisedButton
							label="Login"
							primary={true}
							style={{ margin: 12 }}
							onClick={this.onRegisterClick.bind(this)} />

					</div>
				</MuiThemeProvider>
			);
		}
		else {
			return (
				<MuiThemeProvider>
					<div className="chat">
						<AppBar
							title={"Chat App, Users online: " + this.state.usercount}
							iconElementLeft={<IconButton onClick={this.onBackClick.bind(this)}><NavigationClose /></IconButton>}
						/>
						<hr style={{ backgroundColor: this.state.color }} />
						<List style={{ overflow: '' }}>
							{this.state.messages.map((message) =>
								<div key={message.id}>
									<ListItem
										style={{ marginTop: 1, backgroundColor: message.color }}
										primaryText={message.message}
										secondaryTextLines={1}
										secondaryText={message.time + ":  " + message.username}
										disabled>
									</ListItem>
								</div>
							)}

						</List><br />
						<div 
							ref={input => {this.message_input = input;}}>
							<TextField
								style={{flex:0.8}}
								value={this.state.message_text}
								hintText="Message."
								errorText=""
								multiLine
								onChange={this.messageHandleChange}
							/>
							<RaisedButton
								label="Send"
								primary={true}
								style={{ margin: 12, flex:0.2 }}
								onClick={this.onSendClick.bind(this)} />
						</div>
					</div>
				</MuiThemeProvider>
			);
		}
	}


	componentDidUpdate() {
		this.ensureVisible();
	}

	ensureVisible() {
		if (this.state.prevId === this.state.id)
			return;
		if (this.state.username !== ""){
			this.message_input.scrollIntoView();
			this.setState({prevId: this.state.id})
		}
	}

	user_server(endpoint) {
		// console.log(endpoint)
		fetch(
			baseUrl + endpoint,
			{
				method: 'POST',
				headers: {
					Accept: 'application/json',
					'Content-Type': 'application/json',
				},
				body: JSON.stringify({
					username: this.state.username_text
				}),
			}
		)
			.then((response) => response.json())
			.then((responseJson) => {
				if (responseJson.status === "success") {
					this.setState({
						username: responseJson.username,
						color: responseJson.color
					});
					if (endpoint === "/logout"){
						this.setState({
							username_text: "",
							message_text: "",
							username: "",
							id: -1,
							color: "#444444",
							messages: [],
						})
					}
				}
				else {
					alert('Choose different username!');
				}
			})
			.catch((error) => {
				console.log(error);
			});
	}

	async get_messages() {

		if (this.state.username === "")
			return;

		fetch(
			baseUrl + '/get_message',
			{
				method: 'POST',
				headers: {
					Accept: 'application/json',
					'Content-Type': 'application/json',
				},
				body: JSON.stringify({
					id: this.state.id
				}),
			}
		)
			.then((response) => response.json())
			.then((responseJson) => {
				if (responseJson.status === "success") {
					if (this.state.messages.length !== 0 && responseJson.messages.length !==0)
						if (this.state.messages[this.state.messages.length - 1].id === responseJson.messages[responseJson.messages.length -1].id)
							return;
					this.setState(prevState => {
						return {
							messages: prevState.messages.concat(responseJson.messages),
							id: responseJson.id,
							newMessage: true,
							usercount: responseJson.usercount
						}
					});
					
				}
				else {
					alert('Error retrieving messages!');
				}
			})
			.catch((error) => {
				console.error(error);
			}
			);
	}

	sendMessage() {
		// console.log("responseJson.messages");

		if (this.state.username === "")
			return;

		if (this.state.message_text === "") {
			return;
		}

		fetch(
			baseUrl + '/send_message',
			{
				method: 'POST',
				headers: {
					Accept: 'application/json',
					'Content-Type': 'application/json',
				},
				body: JSON.stringify({
					username: this.state.username,
					message: this.state.message_text
				}),
			}
		)
			.then((response) => response.json())
			.then((responseJson) => {
				if (responseJson.status === "fail") {
					alert('Error sending message! Reload the page!');
				}
				else {
					this.setState({ message_text: '' });
				}
			})
			.catch((error) => {
				console.error(error);
			}
			);
	}

	onRegisterClick() {
		// console.log('onRegister');
		this.user_server("/register");
	}

	onBackClick() {
		// console.log('onBack');
		this.user_server("/logout");
	}

	onSendClick() {
		// console.log('onSend');
		this.sendMessage();
	}

}

// ============================================================

ReactDOM.render(
	<ChatApp />,
	document.getElementById('root')
);



